edura algorítmica, rastreia os principais temas relacionados às imagens e oferece ao usuário os filtros temáticos correspondentes, sendo possível assim verificar quais assuntos foram mais ilustrados.")]),_c('h2',[_vm._v("Imagens em destaque")]),_c('p',[_vm._v("Todas as imagens publicadas estão creditadas a partir do nome do usuário que a publicou no Instagram (sendo possível acessar a página do autor). Como várias imagens são viralizadas e repetidas, adotou-se o critério de creditar a primeira publicação referente àquela imagem.")]),_c('h2',[_vm._v("Segunda etapa: uso de inteligência artificial")]),_c('p',[_vm._v("A segunda fase do projeto apresentará uma curadoria de imagens selecionadas a partir de seis categorias estéticas preestabelecidas pela autora: factuais; ilustração digital; ilustração manual; tipografia digital; tipografia vernacular e apropriação. Utilizando inteligência artificial e aprendizagem de máquina na criação de classificadores de imagens, o site publicará narrativas visuais organizadas a partir de categorias estéticas, possibilitando assim a apreciação das diversas linguagens visuais das imagens dissidentes das redes. Esta etapa do projeto está prevista para ir ao ar em junho de 2020.")]),_c('h2',[_vm._v("Equipe interdisciplinar")]),_vm._m(1),_vm._m(2)])],1)}
