var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"container"},[_c('p',[_vm._v("“Pantone Político” é um projeto concebido inicialmente para o Instagram. Iniciado durante a pandemia do coronavírus no Brasil, pretende dar visibilidade aos principais acontecimentos do período informando sobre a situação do país,mergulhado em uma quádrupla pandemia (sanitária, política, econômica e social). A série é um registro da memória gráfica da política brasileira, na ordem cronológica dos fatos, e contribui de forma criativa, com seu papel social de informar.")]),_c('p',[_vm._v("Pelo fato da comunicação bolsonarista ter utilizado estratégias de disseminação de Fake news para diminuir a gravidade do Covid-19, via redes sociais, referindo-se ao coronavírus como se fosse uma “gripezinha”, a população foi muito prejudicada. Sobretudo os mais vulneráveis, que muitas vezes não têm acesso aos veículos de comunicação confiáveis e se informam no whatsapp, Instagram, Facebook e Youtube.")]),_c('p',[_vm._v("Desde o dia 7 de maio de 2020, o Pantone Político publica diariamente no Instagram as 10 principais notícias de até 10 veículos de comunicação. Devido à velocidade e ao volume de acontecimentos diários, o projeto desenvolveu uma versão streaming, atualizada 24 horas, 7 dias por semana. Para tanto, foi desenvolvido um algoritmo que extrai, sem edição humana, as notícias mais tuitadas e compartilhadas por alguns serviços de comunicação, acerca dos temas abordados pelo projeto. Os perfis dos veículos varridos pelo projeto são: @folha, @uol, @estadao, @estadaopolitica, @midianinja, @elpais_brasil, @JornalOGlobo, @TheInterceptBr, @canalmeio, @CNNBrasil.")]),_c('p',[_vm._v("A partir do momento em que utilizamos o Twitter como fonte da notícia, assumimos a participação política dos usuários desta rede e seu o caráter colaborativo no engajamento e na distribuição dos acontecimentos em tempo real. Nesse sentido, o projeto se insere dentro do projeto do "),_c('a',{attrs:{"href":"/"}},[_vm._v("Calendário Dissidente")]),_vm._v(" como mais uma narrativa visual dissidente.")]),_c('h2',[_vm._v("Escalas tonais")]),_c('p',[_vm._v("O Pantone político, joga luz (e cor) no apocalipse político e pandêmico de 2020, da memória política brasileira e do obscurantismo do governo Bolsonaro. Por meio da metalinguagem, a edição diária do projeto propõe um jogo semântico entre o factual e a estética da cultura visual das redes. Os títulos das notícias, pílulas tipográficas, são deslocados para cards sequenciais, dispostos em tonalidades cromáticas apropriadas da escala de cores Pantone – referência estética para se falar de cor no mundo do design, da moda e da comunicação. Essas notícias, “embelezadas”, congeladas e plasmadas nos cards coloridos têm também como objetivo arquivar as notícias deste período nebuloso.")]),_c('p',[_vm._v("#pantonepolítico #designativista #política #quarentena #design #brasil #forabolsonaro")])])}]

